<template>
  <div class="exam">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>主页</el-breadcrumb-item>
      <el-breadcrumb-item>{{this.$store.state.type}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{this.$store.state.subtype}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{this.$store.state.select}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'exam',
  created () {
    // 拿到题目类型
    // console.log(this.$store.dispatch())
    this.$store.dispatch('select', {
      select: localStorage.getItem('select'),
      type: localStorage.getItem('type'),
      subtype: localStorage.getItem('subtype'),
      current: localStorage.getItem('current')
    })
  },
  components: {
  },
  data: function () {
    return {

    }
  }
}
</script>

<style scoped>
.exam{
  /*background-color: #ccc;*/
}
</style>



